html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $mainFont;
    font-size: 1.6rem;
    line-height: 1.8;
}
p {
    color: $black;
    font-size: 2rem;
}
.container {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img,
picture {
    width: 100%;
    display: block;
}

h1, h2, h3 {
    font-family: $mainFont;
    margin: calc($separation / 2) 0;
    font-weight: 300;
    text-align: center;
    
}

h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}
ul {
    list-style: none;
    padding: 0;
}

// Buttons
.btn {
    &.btn-warning {
        @include btn($yellow, $white, block);
    }
    &.btn-success {
        @include btn($green, $white, block);
    }
}

// Alerts
.wrap-alerts {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    gap: 1rem;
}
.alert {
    border-radius: $border-radius-10;
    padding: 1rem;
    width: $fit-content;
    &.alert-danger {
        background: rgba($red, .1);
        color: $red;
        border: 1px solid $red;
    }
    &.alert-warning {
        background: rgba($yellow, .1);
        color: $yellow;
        border: 1px solid $yellow;
    }
}